"use client";

import { useEffect, useRef, useState } from "react";

type ReportCheckpoint = {
  id: string;
  label: string;
  status: "ON_TRACK" | "NEEDS_ATTENTION" | "PRIORITY";
  phase: string;
  coachNotes: string;
  commonMisses: string[];
  keyDrills: string[];
};

type FullSwingReport = {
  playerOverview: string;
  scores: {
    overall: number;
    power: number;
    reliability: number;
    consistency: number;
  };
  strengths: string[];
  leaks: string[];
  topFixes: string[];
  checkpoints: ReportCheckpoint[];
  practicePlan14Day: string[];
};

type SavedReport = {
  id: string;
  playerName: string;
  createdAt: string; // ISO string
  report: FullSwingReport;
};

const STORAGE_KEY = "virtual-coach-saved-reports";

function StatusBadge({ status }: { status: ReportCheckpoint["status"] }) {
  let label = "";
  let color = "";

  switch (status) {
    case "ON_TRACK":
      label = "On Track";
      color = "bg-emerald-500/15 text-emerald-300 border-emerald-500/40";
      break;
    case "NEEDS_ATTENTION":
      label = "Needs Attention";
      color = "bg-amber-500/15 text-amber-300 border-amber-500/40";
      break;
    case "PRIORITY":
      label = "Priority";
      color = "bg-red-500/15 text-red-300 border-red-500/40";
      break;
    default:
      label = status;
      color = "bg-slate-700/60 text-slate-200 border-slate-500/40";
  }

  return (
    <span
      className={`inline-flex items-center rounded-full border px-2 py-[2px] text-[10px] font-semibold uppercase tracking-wide ${color}`}
    >
      {label}
    </span>
  );
}

export default function CoachPage() {
  const [playerName, setPlayerName] = useState("Player");
  const [age, setAge] = useState("");
  const [handicap, setHandicap] = useState("");
  const [typicalBallFlight, setTypicalBallFlight] = useState("Slight fade");
  const [typicalMiss, setTypicalMiss] = useState("Low pull");
  const [goals, setGoals] = useState(
    "More fairways with driver and more solid irons."
  );
  const [notes, setNotes] = useState(
    "Struggles with consistency under pressure and with longer clubs."
  );

  const [report, setReport] = useState<FullSwingReport | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const [audioLoading, setAudioLoading] = useState(false);
  const audioRef = useRef<HTMLAudioElement | null>(null);

  const [savedReports, setSavedReports] = useState<SavedReport[]>([]);
  const [saving, setSaving] = useState(false);

  // Video upload state
  const [videoUrl, setVideoUrl] = useState<string | null>(null);
  const [videoName, setVideoName] = useState<string | null>(null);
  const previousVideoUrl = useRef<string | null>(null);

  // Load saved reports from localStorage on mount
  useEffect(() => {
    if (typeof window === "undefined") return;
    try {
      const raw = window.localStorage.getItem(STORAGE_KEY);
      if (!raw) return;
      const parsed = JSON.parse(raw) as SavedReport[];
      if (Array.isArray(parsed)) {
        setSavedReports(parsed);
      }
    } catch {
      // ignore
    }
  }, []);

  // Save reports to localStorage when list changes
  useEffect(() => {
    if (typeof window === "undefined") return;
    try {
      window.localStorage.setItem(STORAGE_KEY, JSON.stringify(savedReports));
    } catch {
      // ignore
    }
  }, [savedReports]);

  // Cleanup old object URLs when video changes/unmounts
  useEffect(() => {
    return () => {
      if (previousVideoUrl.current) {
        URL.revokeObjectURL(previousVideoUrl.current);
      }
    };
  }, []);

  async function handleGenerateReport() {
    setLoading(true);
    setError(null);
    setReport(null);

    try {
      const res = await fetch("/api/full-swing-report", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          playerName,
          age,
          handicap,
          typicalBallFlight,
          typicalMiss,
          goals,
          notes,
        }),
      });

      const data = await res.json();

      if (!res.ok || !data.ok) {
        throw new Error(data.error || "Request failed");
      }

      setReport(data.report as FullSwingReport);
    } catch (e: any) {
      console.error(e);
      setError(e.message || "Something went wrong.");
    } finally {
      setLoading(false);
    }
  }

  async function handlePlayOverview() {
    if (!report || !report.playerOverview) return;

    setAudioLoading(true);
    try {
      const res = await fetch("/api/tts", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ text: report.playerOverview }),
      });

      const data = await res.json();

      if (!res.ok || !data.ok) {
        throw new Error(data.error || "TTS request failed");
      }

      const url = `data:${data.mimeType || "audio/mpeg"};base64,${data.audio}`;

      if (audioRef.current) {
        audioRef.current.pause();
      }

      const audio = new Audio(url);
      audioRef.current = audio;
      audio.play();
    } catch (e: any) {
      console.error(e);
      setError(e.message || "Failed to play audio.");
    } finally {
      setAudioLoading(false);
    }
  }

  function handleResetForm() {
    setPlayerName("Player");
    setAge("");
    setHandicap("");
    setTypicalBallFlight("Slight fade");
    setTypicalMiss("Low pull");
    setGoals("More fairways with driver and more solid irons.");
    setNotes("Struggles with consistency under pressure and with longer clubs.");
    setReport(null);
    setError(null);
    setVideoUrl(null);
    setVideoName(null);
  }

  function handlePrintReport() {
    if (!report) {
      setError("Generate a report before printing.");
      return;
    }
    if (typeof window !== "undefined") {
      window.print();
    }
  }

  function handleSaveReport() {
    if (!report) {
      setError("Generate a report before saving.");
      return;
    }
    setSaving(true);
    try {
      const id = `${Date.now()}`;
      const createdAt = new Date().toISOString();
      const newSaved: SavedReport = {
        id,
        playerName: playerName || "Player",
        createdAt,
        report,
      };
      setSavedReports((prev) => [newSaved, ...prev].slice(0, 20)); // keep last 20
    } finally {
      setSaving(false);
    }
  }

  function handleLoadSavedReport(saved: SavedReport) {
    setReport(saved.report);
    setPlayerName(saved.playerName);
    setError(null);
  }

  async function handleCopyDrill(drill: string) {
    try {
      if (typeof navigator !== "undefined" && navigator.clipboard) {
        await navigator.clipboard.writeText(drill);
      }
    } catch (e) {
      console.error("clipboard error", e);
    }
  }

  function handleVideoChange(e: React.ChangeEvent<HTMLInputElement>) {
    const file = e.target.files?.[0];
    if (!file) {
      setVideoUrl(null);
      setVideoName(null);
      return;
    }

    // Revoke previous URL
    if (previousVideoUrl.current) {
      URL.revokeObjectURL(previousVideoUrl.current);
    }

    const url = URL.createObjectURL(file);
    previousVideoUrl.current = url;
    setVideoUrl(url);
    setVideoName(file.name);
  }

  return (
    <div className="min-h-screen bg-slate-950 text-slate-100">
      <div className="mx-auto flex w-full max-w-6xl flex-col gap-4 p-6 pb-10">
        {/* Top navigation bar */}
        <nav className="mb-1 flex items-center justify-between rounded-full border border-slate-800 bg-slate-950/80 px-4 py-2 text-xs">
          <div className="flex items-center gap-3">
            <a
              href="/"
              className="rounded-full bg-slate-900 px-3 py-1 font-semibold text-slate-100 hover:bg-slate-800"
            >
              ← Back to home
            </a>
            <button
              onClick={handleResetForm}
              className="rounded-full bg-slate-900 px-3 py-1 font-semibold text-slate-100 hover:bg-slate-800"
            >
              Upload another swing
            </button>
          </div>
          <div className="flex items-center gap-2">
            <button
              onClick={handleSaveReport}
              disabled={saving}
              className="rounded-full bg-slate-800 px-3 py-1 font-semibold text-slate-100 disabled:opacity-60"
            >
              {saving ? "Saving..." : "Save report"}
            </button>
            <button
              onClick={handlePrintReport}
              className="rounded-full bg-emerald-500 px-3 py-1 font-semibold text-emerald-950 hover:bg-emerald-400"
            >
              Print / PDF
            </button>
          </div>
        </nav>

        {/* Header */}
        <header className="flex flex-col gap-2 border-b border-slate-800 pb-4">
          <h1 className="text-3xl font-bold tracking-tight">Virtual Coach AI</h1>
          <p className="max-w-3xl text-sm text-slate-300">
            Generate a structured full-swing report from a single set of notes. Fill
            in the player details, attach a swing video, hit{" "}
            <span className="font-semibold">Generate Report</span>, and the AI will
            build an overview, checkpoints, and a 14-day practice plan.
          </p>
        </header>

        {/* Input form + video upload */}
        <section className="grid gap-4 rounded-2xl border border-slate-800 bg-slate-900/40 p-5 md:grid-cols-3">
          <div className="space-y-3 md:col-span-2">
            <div className="grid gap-3 md:grid-cols-3">
              <div>
                <label className="text-xs font-medium text-slate-300">
                  Player name
                </label>
                <input
                  className="mt-1 w-full rounded-md border border-slate-700 bg-slate-900 px-2 py-1.5 text-sm outline-none focus:border-emerald-500"
                  value={playerName}
                  onChange={(e) => setPlayerName(e.target.value)}
                />
              </div>
              <div>
                <label className="text-xs font-medium text-slate-300">Age</label>
                <input
                  className="mt-1 w-full rounded-md border border-slate-700 bg-slate-900 px-2 py-1.5 text-sm outline-none focus:border-emerald-500"
                  value={age}
                  onChange={(e) => setAge(e.target.value)}
                />
              </div>
              <div>
                <label className="text-xs font-medium text-slate-300">
                  Handicap / scoring average
                </label>
                <input
                  className="mt-1 w-full rounded-md border border-slate-700 bg-slate-900 px-2 py-1.5 text-sm outline-none focus:border-emerald-500"
                  value={handicap}
                  onChange={(e) => setHandicap(e.target.value)}
                />
              </div>
            </div>

            <div className="grid gap-3 md:grid-cols-2">
              <div>
                <label className="text-xs font-medium text-slate-300">
                  Typical ball flight
                </label>
                <input
                  className="mt-1 w-full rounded-md border border-slate-700 bg-slate-900 px-2 py-1.5 text-sm outline-none focus:border-emerald-500"
                  value={typicalBallFlight}
                  onChange={(e) => setTypicalBallFlight(e.target.value)}
                />
              </div>
              <div>
                <label className="text-xs font-medium text-slate-300">
                  Most common miss
                </label>
                <input
                  className="mt-1 w-full rounded-md border border-slate-700 bg-slate-900 px-2 py-1.5 text-sm outline-none focus:border-emerald-500"
                  value={typicalMiss}
                  onChange={(e) => setTypicalMiss(e.target.value)}
                />
              </div>
            </div>

            <div>
              <label className="text-xs font-medium text-slate-300">
                Goals for your full swing
              </label>
              <textarea
                className="mt-1 w-full rounded-md border border-slate-700 bg-slate-900 px-2 py-2 text-sm outline-none focus:border-emerald-500"
                rows={2}
                value={goals}
                onChange={(e) => setGoals(e.target.value)}
              />
            </div>

            <div>
              <label className="text-xs font-medium text-slate-300">
                Notes from lesson / self-diagnosis
              </label>
              <textarea
                className="mt-1 w-full rounded-md border border-slate-700 bg-slate-900 px-2 py-2 text-sm outline-none focus:border-emerald-500"
                rows={3}
                value={notes}
                onChange={(e) => setNotes(e.target.value)}
              />
            </div>
          </div>

          <div className="flex flex-col justify-between gap-4 rounded-xl border border-slate-800 bg-slate-900/60 p-4">
            <div className="space-y-3 text-sm text-slate-300">
              <p className="font-semibold text-slate-100">
                How this report is generated
              </p>
              <p className="text-xs text-slate-300">
                Your inputs (and optionally your swing video) stay on your device.
                The written notes are sent securely to Virtual Coach AI to build a
                structured report: scores, checkpoints, and a 14-day practice plan.
              </p>

              <div className="mt-2 space-y-1">
                <label className="text-xs font-medium text-slate-300">
                  Swing video (optional)
                </label>
                <input
                  type="file"
                  accept="video/*"
                  onChange={handleVideoChange}
                  className="block w-full text-[11px] text-slate-300 file:mr-3 file:rounded-md file:border-0 file:bg-slate-800 file:px-3 file:py-1.5 file:text-xs file:font-semibold file:text-slate-100 hover:file:bg-slate-700"
                />
                {videoName && (
                  <p className="text-[11px] text-slate-400 truncate">
                    Selected: {videoName}
                  </p>
                )}
              </div>
            </div>
            <button
              onClick={handleGenerateReport}
              disabled={loading}
              className="mt-2 inline-flex items-center justify-center rounded-md bg-emerald-500 px-4 py-2 text-sm font-semibold text-emerald-950 shadow-sm disabled:opacity-60"
            >
              {loading ? "Generating report..." : "Generate Full Swing Report"}
            </button>
            {error && (
              <p className="text-xs text-red-400">Error: {error}</p>
            )}
          </div>
        </section>

        {/* Saved reports list */}
        {savedReports.length > 0 && (
          <section className="rounded-2xl border border-slate-800 bg-slate-900/40 p-4">
            <h2 className="text-sm font-semibold tracking-tight text-slate-100">
              Saved reports
            </h2>
            <p className="mt-1 text-[11px] text-slate-400">
              Click a report to reload it. Newest reports appear first.
            </p>
            <div className="mt-2 flex flex-wrap gap-2 text-[11px]">
              {savedReports.map((sr) => (
                <button
                  key={sr.id}
                  onClick={() => handleLoadSavedReport(sr)}
                  className="rounded-full border border-slate-700 bg-slate-900 px-3 py-1 text-left text-slate-200 hover:border-emerald-500/60"
                >
                  <span className="font-semibold mr-1">{sr.playerName}</span>
                  <span className="text-slate-400">
                    {new Date(sr.createdAt).toLocaleString()}
                  </span>
                </button>
              ))}
            </div>
          </section>
        )}

        {/* Video preview */}
        {videoUrl && (
          <section className="rounded-2xl border border-slate-800 bg-slate-900/60 p-4">
            <div className="mb-2 flex items-center justify-between gap-2">
              <h2 className="text-sm font-semibold tracking-tight text-slate-100">
                Swing video preview
              </h2>
              <p className="text-[11px] text-slate-400">
                This video is only loaded locally in your browser.
              </p>
            </div>
            <div className="rounded-xl bg-black">
              <video
                controls
                src={videoUrl}
                className="aspect-video w-full rounded-xl"
              />
            </div>
          </section>
        )}

        {/* Report output */}
        {report && (
          <section className="grid gap-4 md:grid-cols-[2fr,1.1fr]">
            {/* Left: Overview + checkpoints */}
            <div className="space-y-4">
              {/* Player overview */}
              <div className="rounded-2xl border border-slate-800 bg-slate-900/50 p-4">
                <div className="flex flex-wrap items-center justify-between gap-2">
                  <div>
                    <h2 className="text-lg font-semibold tracking-tight">
                      Player Overview
                    </h2>
                    <p className="mt-1 text-[11px] text-slate-400">
                      High-level summary based on your notes and swing goals.
                    </p>
                  </div>
                  <div className="flex items-center gap-2">
                    <button
                      onClick={handlePlayOverview}
                      disabled={audioLoading}
                      className="inline-flex items-center rounded-full bg-slate-800 px-3 py-1 text-[11px] font-semibold text-slate-100 disabled:opacity-60"
                    >
                      {audioLoading ? "Preparing audio..." : "▶ Play overview"}
                    </button>
                    <span className="rounded-full bg-emerald-500/10 px-2 py-[2px] text-[10px] font-semibold uppercase tracking-wide text-emerald-300">
                      Generated by Virtual Coach AI
                    </span>
                  </div>
                </div>
                <p className="mt-3 text-sm leading-relaxed text-slate-200">
                  {report.playerOverview}
                </p>
              </div>

              {/* Checkpoints */}
              <div className="rounded-2xl border border-slate-800 bg-slate-900/50 p-4">
                <div className="mb-3 flex items-center justify-between">
                  <h3 className="text-sm font-semibold tracking-tight text-slate-100">
                    P1–P9 Checkpoints
                  </h3>
                  <p className="text-[11px] text-slate-400">
                    Use these checkpoints to guide swing changes.
                  </p>
                </div>

                <div className="space-y-3">
                  {report.checkpoints.map((cp) => (
                    <div
                      key={cp.id}
                      className="rounded-xl border border-slate-800 bg-slate-950/60 p-3"
                    >
                      <div className="flex flex-wrap items-center justify-between gap-2">
                        <div>
                          <p className="text-xs font-semibold uppercase tracking-wide text-slate-400">
                            {cp.phase}
                          </p>
                          <h4 className="text-sm font-semibold text-slate-100">
                            {cp.label}
                          </h4>
                        </div>
                        <StatusBadge status={cp.status} />
                      </div>

                      <p className="mt-2 text-[13px] leading-relaxed text-slate-200">
                        {cp.coachNotes}
                      </p>

                      <div className="mt-2 grid gap-2 text-[11px] text-slate-300 md:grid-cols-3">
                        <div>
                          <p className="font-semibold text-slate-200">
                            Common misses
                          </p>
                          <ul className="mt-1 space-y-[2px]">
                            {cp.commonMisses.map((m, i) => (
                              <li key={i}>• {m}</li>
                            ))}
                          </ul>
                        </div>
                        <div className="md:col-span-2">
                          <p className="font-semibold text-slate-200">
                            Key drills
                          </p>
                          <ul className="mt-1 space-y-[2px]">
                            {cp.keyDrills.map((d, i) => (
                              <li
                                key={i}
                                className="flex items-center justify-between gap-2"
                              >
                                <span>• {d}</span>
                                <span className="flex shrink-0 gap-1">
                                  <button
                                    onClick={() => handleCopyDrill(d)}
                                    className="rounded-full bg-slate-800 px-2 py-[1px] text-[10px] hover:bg-slate-700"
                                    title="Copy drill text"
                                  >
                                    Copy
                                  </button>
                                  <a
                                    href={`https://www.youtube.com/results?search_query=${encodeURIComponent(
                                      d
                                    )}`}
                                    target="_blank"
                                    rel="noreferrer"
                                    className="rounded-full bg-slate-800 px-2 py-[1px] text-[10px] hover:bg-slate-700"
                                    title="Search drill on YouTube"
                                  >
                                    YouTube
                                  </a>
                                </span>
                              </li>
                            ))}
                          </ul>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>

            {/* Right: Scores + lists + practice plan */}
            <div className="space-y-4">
              {/* Scores */}
              <div className="rounded-2xl border border-slate-800 bg-slate-900/50 p-4">
                <h3 className="text-sm font-semibold tracking-tight text-slate-100">
                  Power &amp; Reliability
                </h3>
                <p className="mt-1 text-[11px] text-slate-400">
                  Scores are estimates based on your notes and described patterns.
                </p>

                <div className="mt-3 space-y-2">
                  <ScoreRow label="Overall" value={report.scores.overall} />
                  <ScoreRow label="Power" value={report.scores.power} />
                  <ScoreRow
                    label="Reliability"
                    value={report.scores.reliability}
                  />
                  <ScoreRow
                    label="Consistency"
                    value={report.scores.consistency}
                  />
                </div>
              </div>

              {/* Strengths / leaks / top fixes */}
              <div className="rounded-2xl border border-slate-800 bg-slate-900/50 p-4 space-y-3 text-sm">
                <ListBlock title="What you do well" items={report.strengths} />
                <ListBlock title="Where your leaks are" items={report.leaks} />
                <ListBlock title="Top 3 fixes" items={report.topFixes} numbered />
              </div>

              {/* Practice plan */}
              <div className="rounded-2xl border border-slate-800 bg-slate-900/50 p-4">
                <h3 className="text-sm font-semibold tracking-tight text-slate-100">
                  14-Day Practice Plan
                </h3>
                <p className="mt-1 text-[11px] text-slate-400">
                  Treat each item as a focused, 15–20 minute session.
                </p>
                <ol className="mt-2 space-y-1 text-[13px] text-slate-200">
                  {report.practicePlan14Day.map((item, i) => (
                    <li key={i}>
                      <span className="mr-1 text-slate-400">
                        {String(i + 1).padStart(2, "0")}:
                      </span>
                      {item}
                    </li>
                  ))}
                </ol>
              </div>
            </div>
          </section>
        )}
      </div>
    </div>
  );
}

function ScoreRow({ label, value }: { label: string; value: number }) {
  const clamped = Math.max(0, Math.min(100, value ?? 0));

  return (
    <div className="flex items-center justify-between gap-2 text-xs">
      <span className="text-slate-200">{label}</span>
      <div className="flex items-center gap-2">
        <div className="h-1.5 w-24 overflow-hidden rounded-full bg-slate-800">
          <div
            className="h-full rounded-full bg-emerald-500"
            style={{ width: `${clamped}%` }}
          />
        </div>
        <span className="w-8 text-right text-slate-300">{clamped}</span>
      </div>
    </div>
  );
}

function ListBlock({
  title,
  items,
  numbered,
}: {
  title: string;
  items: string[];
  numbered?: boolean;
}) {
  return (
    <div>
      <p className="text-xs font-semibold uppercase tracking-wide text-slate-400">
        {title}
      </p>
      {numbered ? (
        <ol className="mt-1 space-y-[2px] text-[13px] text-slate-200">
          {items.map((item, i) => (
            <li key={i}>
              <span className="mr-1 text-slate-400">
                {String(i + 1).padStart(2, "0")}:
              </span>
              {item}
            </li>
          ))}
        </ol>
      ) : (
        <ul className="mt-1 space-y-[2px] text-[13px] text-slate-200">
          {items.map((item, i) => (
            <li key={i}>• {item}</li>
          ))}
        </ul>
      )}
    </div>
  );
}
